#questListContainer {
  transition: 0.5s;
  width: 98%;
  margin: 0 auto;
  height: 800px;
  // 반응형
  @media (max-height: 950px) {
    height: 600px;
    transition: 0.5s;
  }
  @media (max-height: 750px) {
    height: 500px;
    transition: 0.5s;
  }
  @media (max-height: 650px) {
    height: 300px;
    transition: 0.5s;
  }

  overflow: auto;
  .visible {
    display: block;
  }
  .unvisible {
    display: none;
  }

  .questListDiv {
    &:first-child {
      border-bottom: 1px solid lightgray;
    }
    & + .questListDiv {
      border-bottom: 1px solid lightgray;
    }
  }
  .questList {
    display: flex;
    align-items: center;
    margin: 10px 0;
    box-sizing: border-box;

    &:last-child {
      border-bottom: none;
    }
    .question {
      width: 40%;
    }

    .answerList {
      width: 15%;
      height: 100%;
      font-size: 1.4rem;
      font-weight: 500;
      text-align: center;
      padding: 0.25rem 0.25rem;
      border-radius: 1rem;
      box-sizing: border-box;
      &.answer {
        background: lightblue;
      }
    }
  }
}
